@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@300&display=swap");
* {
  font-family: "Noto Sans", sans-serif;
}
body{
  background-image: url(https://transparenttextures.com/patterns/brick-wall.png);
}
.heading{
  text-align: center;
}
.App {
  font-family: sans-serif;
  text-align: center;
}
input {
  width: 80%;
  background: #fff;
  position: -webkit-sticky;
  position: sticky;
  border-radius: 1vw;
  padding: 1vw;
  font-weight: bolder;
  font-size: 1.3vw;
  top: 0;
  margin: 0 auto 1vw;
}


.container {
  width: 99vw;
  height: 95vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

.bookcontainer {
  width: 100%;
  flex-direction: column;
  display: flex;
  overflow: hidden;
  height: 200%;
  padding: 2vw 0vw;
  overflow-y: scroll;
  /* margin: auto; */
}

.books {
  display: inline-block;
  background-image: url(https://transparenttextures.com/patterns/dark-stripes-light.png);
  font-size: 1vw;
  width: 100%;
  margin: 1.1vw 5vw;
  padding: 0.5vw 0.8vw;
  box-shadow: 0 1px 2px 0;
  transition: all .4s ease-in-out;
  width: 90%;
}
.books h2 {
  width: 30%;
}
.books h3 {
  width: 30%;
  text-align: center;
}
.books p {
  width: 30%;
  text-align: end;
  text-decoration: underline;
}
.books div {
  display: flex;
  justify-content: space-around;
}
.books:hover {
  box-shadow: 2px 2px 5px 0px;
}

@media (max-width: 600px){
  .books {
    font-size: 2.5vw;
    margin: 3vw 4vw;
  }
  input {
    font-size: 4vw;
}

}